import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, showToast, WARNING, WindowComponent } from 'summer';
import { CLASS_NAME, QuadroSocietario } from '../../../../models/dtos/mcg/quadroSocietario';
import { INDICADOR_QUALIFICACAO_SOCIO_ENUM_VALUES } from '../../../../models/enumerated/mcg/indicadorQualificacaoSocioEnum';
import { QUALIFICACAO_REPRESENTANTE_LEGAL_ENUM_VALUES } from '../../../../models/enumerated/mcg/qualificacaoRepresentanteLegalEnum';
import { QUALIFICACAO_SOCIO_ENUM_VALUES } from '../../../../models/enumerated/mcg/qualificacaoSocioEnum';
import { validarQuadroSocietario } from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import ResponsavelIS from '../../../components/ger/responsavelIS/ResponsavelIS';

interface QuadroSocietarioEditViewProps extends EditViewProps<QuadroSocietario> {}

const QuadroSocietarioEditView: FC<QuadroSocietarioEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<QuadroSocietario>({
    defaultValues: { empresa: { id: globalReducer.globalParameter.empresa.id }, ...selectedItem },
    mode: 'onBlur',
  });
  const labelWidth = 275;

  const beforePersist = (formData: QuadroSocietario, done: any) => {
    validarQuadroSocietario(formData, {
      thenFunction: (result: any) => {
        let resultErro;
        if (result.length > 0) {
          if (result === 'SocioExiste') {
            resultErro = 'Sócio já foi cadastrado';
          } else if (result === 'Limite100') {
            resultErro = 'A soma dos percentuais ultrapassaram o valor de 100%.';
          }
          showToast(resultErro, WARNING);
        } else {
          done(formData);
        }
      },
    });
    return { async: true };
  };

  return (
    <WindowComponent title="Manutenção do Quadro de Sócios" wikiId={2127}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} beforePersist={beforePersist}>
        <InputGroupInline id="empresa.id" formProps={formProps} hidden={true} />
        <Row>
          <Col>
            <ResponsavelIS
              labelWidth={labelWidth}
              formProps={{ ...formProps, validation: { required: true } }}
              label="Responsável (Sócio)"
              placeholder="Selecione um Responsável"
              id="socio"
              isPrependLabel={true}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              formProps={formProps}
              id="indicadorQualificacaoSocio"
              isPrependLabel={true}
              label="Indicador de Qualificação do Sócio"
              tooltipProps={{ message: 'Indicador de Qualificação do Sócio' }}
              placeholder="Selecione o Indicador de Qualificação do Sócio."
              inputDropDownProps={{
                optionsProvider: INDICADOR_QUALIFICACAO_SOCIO_ENUM_VALUES,
                selectProps: { isClearable: true },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              formProps={formProps}
              id="qualificacaoSocio"
              isPrependLabel={true}
              label="Qualificação do Sócio"
              tooltipProps={{ message: 'Qualificação do Sócio' }}
              placeholder="Selecione a Qualificação do Sócio."
              inputDropDownProps={{
                optionsProvider: QUALIFICACAO_SOCIO_ENUM_VALUES,
                selectProps: { isClearable: true },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <ResponsavelIS
              labelWidth={labelWidth}
              formProps={formProps}
              label="Representante Legal"
              placeholder="Selecione um Representante Legal"
              tooltipProps={{ message: 'Representante Legal' }}
              id="representanteLegal"
              isPrependLabel={true}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              formProps={formProps}
              id="qualificacaoRepresentanteLegal"
              isPrependLabel={true}
              label="Qualificação do Representante"
              tooltipProps={{ message: 'Qualificação do Representante Legal' }}
              placeholder="Selecione a Qualificação do Representante."
              inputDropDownProps={{
                optionsProvider: QUALIFICACAO_REPRESENTANTE_LEGAL_ENUM_VALUES,
                selectProps: { isClearable: true },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              formProps={{ ...formProps, validation: { required: true, min: 0.01, max: 100 } }}
              id="percentual"
              isPrependLabel={true}
              label="Percentual no Capital Total"
              tooltipProps={{ message: 'Percentual Relativo à Participação do Sócio no Capital Total' }}
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              formProps={formProps}
              id="dataAlteracaoQuadro"
              isPrependLabel={true}
              label="Data da Alteração"
              tooltipProps={{ message: 'Data da Alteração no Quadro Societário - Data em que o sócio iniciou sua participação do quadro' }}
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
              id="percentualCapitalVotante"
              isPrependLabel={true}
              label="Percentual no Capital Votante"
              tooltipProps={{ message: 'Percentual Relativo à Participação do Sócio no Capital Votante' }}
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              formProps={formProps}
              id="dataSaidaQuadro"
              isPrependLabel={true}
              label="Data da Saída"
              tooltipProps={{ message: 'Data da Saída do Quadro Societário - Data em que o sócio deixou de participar do quadro' }}
              type={InputType.DATE}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default QuadroSocietarioEditView;
